@import url(https://fonts.googleapis.com/css2?family=IM+Fell+Double+Pica&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-image:url(/tap-room-redux/static/media/taps-pixabay.26c5fbfc.jpg);background-size:cover;background-attachment:fixed;width:100%;height:100%}

.header{font-family:"IM Fell Double Pica",serif;min-height:10vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(40px + 2vmin);color:white}

.form-label{font-family:"Montserrat",sans-serif;font-weight:bold}.form-input{max-width:200px}#content-container{font-family:"Montserrat",sans-serif;font-size:18px;font-weight:bold;background-color:rgba(255,255,255,0.5);margin:auto;padding:30px;max-width:800px;min-height:450px;border-radius:10px}

